.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* App.css */
.flow-button {
  padding: 8px 16px;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  font-family: inherit;
  width: 100%;
  margin-bottom: 8px;
}

.flow-button-primary {
  background-color: #007bff;
}

.flow-button-success {
  background-color: #28a745;
}

.flow-button-secondary {
  background-color: #6c757d;
}

.flow-node {
  padding: 20px;
  border-radius: 5px;
  background: #f0f0f0;
  border: 1px solid #ddd;
  transition: all 0.2s ease;
  min-width: 120px;
  position: relative;
}

.flow-node.selected {
  background: #d4edff;
  border: 2px solid #007bff;
}

.flow-node-label {
  font-size: 14px;
  margin: 5px 0;
  text-align: center;
}

.flow-node-input {
  margin: 5px 0;
  width: 100%;
  font-size: 14px;
}

.flow-node-select {
  width: 100%;
  margin-top: 5px;
  font-size: 14px;
}

.flow-handle-label {
  font-size: 12px;
  color: #333;
  white-space: nowrap;
  z-index: 2;
  position: absolute;
}

.flow-handle-label-top {
  bottom: 22px;
  left: 50%;
  transform: translateX(-50%);
}

.flow-handle-label-bottom {
  top: 22px;
  left: 50%;
  transform: translateX(-50%);
}

.flow-handle-label-left {
  right: 22px;
  top: 50%;
  transform: translateY(-50%);
}

.flow-handle-label-right {
  left: 22px;
  top: 50%;
  transform: translateY(-50%);
}

.sidebar {
  width: 200px;
  background-color: white;
  padding: 16px;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  z-index: 10;
}

.file-upload-label {
  display: block;
  text-align: center;
  margin-bottom: 8px;
}

.file-upload-input {
  display: none;
}

.custom-btn {

  padding: 2px 8px;
  font-size: 14px;
  border: 1px solid #b6b6b6;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.2s;
  background: whitesmoke;
  margin: 2px;

}
.custom-btn:hover {
  background: #b3e5fc;
}

.custom-select {

  background: whitesmoke;
  border: 1px solid #b6b6b6;
  border-radius: 4px;
  padding: 2px 8px;
  font-size: 14px;
  cursor: pointer;
  margin: 2px;
  transition: background 0.2s;

}
.custom-select:focus {
  outline: none;
  background: whitesmoke;
}

.custom-checkbox {
  accent-color: #1a237e;
  width: 18px;
  height: 18px;
  border: 1px solid #b6b6b6;
  border-radius: 4px;
  background: whitesmoke;
  margin: 2px 6px 2px 2px;
  cursor: pointer;
  transition: background 0.2s;
  vertical-align: middle;
}
.custom-checkbox:focus {
  outline: 2px solid #b3e5fc;
  background: #b3e5fc;
}

.sidebar-bottom-btn {
  padding: 14px 0;
  font-size: 15px;
  border-radius: 6px;
  background: whitesmoke;
  border: 1px solid #b6b6b6;
  cursor: pointer;
  transition: background 0.2s;
}
.sidebar-bottom-btn:hover {
  background: #1565c0;
}

.component-border {
  border: 1px solid #b6b6b6;
  border-radius: 8px;
  padding: 2px;
  margin-bottom: 4px;
  display: flex;
  flex-direction: column;
}

.component-border-no-padding {
  border: 1px solid #b6b6b6;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}


.component-border-controls {
  border: 1px solid #b6b6b6;
  border-radius: 8px;
  padding: 2px;
  margin-bottom: 4px;
  display: flex;
  flex-direction: column;
}
.component-border-controls_horix {
  padding: 2px;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  gap: 6px; /* Spazio tra i bottoni */
}

.component-border-controls_horix .custom-btn {
  flex: 1 1 0;
  margin: 0; /* Elimina margini interni ai bottoni */
  min-width: 0;
}
